import { Box, Button, Heading, Image, Text } from '@chakra-ui/react'
import React from 'react'
import logo from '../Images/favicon.png'

const Navbar = () => {
    return (
        <Box className='navbar'>
            <Box className='logo'>
                <Box>
                    <Image src={logo} />
                </Box>
                <Box>
                    <Heading>Atanu K.</Heading>
                </Box>
            </Box>
            <Box display={["none", 'none', 'flex']} className="nav-options">
                <Box>
                    <Button><span>Home</span></Button>
                </Box>
                <Box>
                    <Button><span>About me</span></Button>
                </Box>
                <Box>
                    <Button><span>Skills</span></Button>
                </Box>
                <Box>
                    <Button><span>Project</span></Button>
                </Box>
                <Box>
                    <Button><span>Contact</span></Button>
                </Box>
                <Box>
                    <Button><span>Resume</span></Button>
                </Box>
            </Box>
        </Box>
    )
}

export default Navbar